import styled from 'styled-components'
export const TabWrapper = styled.div`
  .tabbar {
    position: fixed;
    bottom: 0px;
    left: 0;
    width: 100%;
    background-color: white;
    z-index: 999;
    .add {
      background-color: red;
      border-radius: 22%;
    }
  }
  .adm-tab-bar-wrap {
    display: flex;
    align-items: center;
    justify-content: space-around;
  }
  .adm-tab-bar-item {
    flex: 0;
  }
  .adm-tab-bar-item-title {
    font-size: 20px;
    line-height: 20px;
  }
`
