import { createSlice, createAsyncThunk } from '@reduxjs/toolkit'
import http from '../../utils/http'

//home
export const fetchdata = createAsyncThunk(
  '/home/multidata',
  async (currentPage = 1) => {
    try {
      const res = await http.get('/moment', {
        params: { page: currentPage },
      })
      console.log(res.data)
      return res.data
    } catch (error) {
      console.error('Error fetching data:', error)
      throw error //  createAsyncThunk 
    }
  }
)
//collect
export const fetchdata02 = createAsyncThunk(
  '/user/my/collect/list',
  async () => {
    const res = await http.get('/user/my/collect/list')
    return res.data.data
  }
)
//follow
export const fetchdata03 = createAsyncThunk(
  '/user/my/follow/data',
  async () => {
    const res = await http.get('/user/my/follow')
    return res.data.data
  }
)
//
export const fetchdata04 = createAsyncThunk('/moment/my/note', async () => {
  const res = await http.get('/moment/my/note')
  return res.data.data
})
//
export const fetchdata05 = createAsyncThunk(
  '/moment/my/notefavor',
  async () => {
    const res = await http.get('/moment/my/notefavor')
    return res.data.data
  }
)
//homemoment
export const favorMoment = createAsyncThunk(
  '/home/favor',
  async (moment_id) => {
    const res = await http.post('/my/favor', { moment_id })
    return res.data.data[0].add
  }
)
//home
export const detailMoment = createAsyncThunk(
  'home/detail',
  async (momentId) => {
    const res = await http.get(`/moment/${momentId}`)
    return res.data.data
  }
)
//
export const favorComment = createAsyncThunk(
  'my/comment/reply/my',
  async ({ commentId, favor }) => {
    const res = await http.post('/my/comment/reply/my', { commentId, favor })
    return res.data
  }
)
//
export const userFollow = createAsyncThunk(
  '/user/my/follow',
  async ({ id, beFollowedUser }) => {
    const res = await http.post('/user/my/follow', { id, beFollowedUser })
    console.log(res.data)
    return res.data
  }
)
//
export const userCollect = createAsyncThunk(
  '/user/my/collect',
  async ({ id, momentId }) => {
    const res = await http.post('/user/my/collect', { id, momentId })
    return res.data
  }
)
//
export const comment = createAsyncThunk(
  '/my/comment',
  async ({ content, momentId }) => {
    const res = await http.post('/my/comment', { content, momentId })
    return res.data
  }
)
//
export const replyComment = createAsyncThunk(
  '/my/comment/reply',
  async ({ content, momentId, commentId }) => {
    const res = await http.post('/my/comment/reply', {
      content,
      momentId,
      commentId,
    })
    return res.data
  }
)
//
export const delComment = createAsyncThunk(
  '/my/comment/del',
  async ({ commentId }) => {
    console.log(commentId)
    if (!commentId) return
    const res = await http.post('/my/comment/del', {
      commentId,
    })
    return res.data
  }
)

const homeSlice = createSlice({
  name: 'home',
  initialState: {
    detaildata: [],
    collectMoments: [],
    followMoments: [],
    total: -1,
    add: '',
    itemdetail: [],
    listNotes: [],
    ListFavorNotes: [],
    scrollY: 0,
  },
  reducers: {
    cleardata(state) {
      state.detaildata = []
    },
    setScrollPosition: (state, { payload }) => {
      state.scrollY = payload
    },
  },
  extraReducers: (builder) => {
    builder
      .addCase(fetchdata.fulfilled, (state, { payload }) => {
        state.detaildata = [...state.detaildata, ...payload.data.data]
        state.total = payload.data.total
      })
      .addCase(favorMoment.fulfilled, (state, { payload }) => {
        state.add = payload
      })
      .addCase(detailMoment.fulfilled, (state, { payload }) => {
        state.itemdetail = payload
      })
      .addCase(fetchdata02.fulfilled, (state, { payload }) => {
        state.collectMoments = payload
      })
      .addCase(fetchdata03.fulfilled, (state, { payload }) => {
        state.followMoments = payload
      })
      .addCase(fetchdata04.fulfilled, (state, { payload }) => {
        state.listNotes = payload
      })
      .addCase(fetchdata05.fulfilled, (state, { payload }) => {
        state.ListFavorNotes = payload
      })
      .addDefaultCase((state, action) => {})
  },
})
export const { cleardata, setScrollPosition } = homeSlice.actions
export default homeSlice.reducer
