import { Route, Routes, useLocation } from 'react-router-dom'
import Tabbar from './components/tabbar'
import { Navigate, useNavigate } from 'react-router-dom'
import './assets/css/reset.css'
import React from 'react'
import istokenexpire from './components/istokenexpire'
const Home = React.lazy(() => import('./pages/home'))
const Add = React.lazy(() => import('./pages/add'))
const Mine = React.lazy(() => import('./pages/mine'))
const Login = React.lazy(() => import('./pages/login'))
const Search = React.lazy(() => import('./pages/searchpage'))
const Detail = React.lazy(() => import('./pages/home/detail/detail'))
const Edit = React.lazy(() => import('./pages/edit'))

function App() {
  const navigate = useNavigate()
  const location = useLocation()
  return (
    <div className="app">
      <div>
        {location.pathname === '/search' ||
        location.pathname === '/add' ||
        location.pathname === '/edit' ||
        location.pathname === '/login' ||
        /^\/home\/detail\/.*$/.test(location.pathname) ? null : (
          <Tabbar navigate={navigate}></Tabbar>
        )}
      </div>
      <Routes>
        <Route path="/" element={<Navigate to="/home" />}></Route>
        <Route path="/home" element={<Home></Home>}></Route>
        <Route path="/home/detail/:id" element={<Detail />}></Route>
        <Route
          path="/add"
          element={
            istokenexpire() ? (
              <Navigate to="/login" />
            ) : (
              <Add navigate={navigate}></Add>
            )
          }
        ></Route>
        <Route path="/mine" element={<Mine navigate={navigate} />}></Route>
        <Route
          path="/login"
          element={<Login navigate={navigate}></Login>}
        ></Route>
        <Route path="/edit" element={<Edit navigate={navigate}></Edit>}></Route>
        <Route
          path="/search"
          element={<Search navigate={navigate}></Search>}
        ></Route>
      </Routes>
    </div>
  )
}

export default App
